@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Anonymous+Pro:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Epilogue:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  box-sizing: inherit; }

:root {
  --fuente-poppins: "Poppins", sans-serif;
  --fuente-inter: "Inter", sans-serif;
  --fuente-anonymus: "Anonymous Pro", monospace;
  --fuente-epilogue: "Epilogue", sans-serif;
  --color-green: #c7ff00;
  --marquee-width: 100%;
  --marquee-height: 20vh;
  /* --marquee-elements: 12; */
  /* defined with JavaScript */
  --marquee-elements-displayed: 1;
  --marquee-element-width: calc(
    var(--marquee-width) / var(--marquee-elements-displayed)
  );
  --marquee-animation-duration: calc(var(--marquee-elements) * 8s);
  /*slider home*/
  --marquee-width1: 100%;
  --marquee-height1: 20vh;
  /* --marquee-elements: 12; */
  /* defined with JavaScript */
  --marquee-elements-displayed1: 0.5;
  --marquee-element-width1: calc(
    var(--marquee-width1) / var(--marquee-elements-displayed1)
  );
  --marquee-animation-duration1: calc(var(--marquee-elements1) * 10s);
  /*segundo slider de market*/
  --marquee-width2: 100%;
  --marquee-height2: 20vh;
  /* --marquee-elements: 12; */
  /* defined with JavaScript */
  --marquee-elements-displayed2: 1;
  --marquee-element-width2: calc(
    var(--marquee-width2) / var(--marquee-elements-displayed2)
  );
  --marquee-animation-duration2: calc(var(--marquee-elements2) * 8s); }
.ver{
  display: block !important;
}
body::-webkit-scrollbar {
  width: 12px;
  /* width of the entire scrollbar */ }

body::-webkit-scrollbar-track {
  background: #393536 !important;
  /* color of the tracking area */ }

.scrollhidden {
  overflow: hidden; }

body::-webkit-scrollbar-thumb {
  background-color: #9B33FF;
  /* color of the scroll thumb */
  border-radius: 20px;
  /* roundness of the scroll thumb */ }

main {
  overflow: hidden; }

.container {
  width: 85%;
  max-width: 1580px;
  margin: 0 auto; }

header {
  padding: 60px 0;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: transparent; 
  transition: 0.5s ease;
}

header::before {
  content: "";
  position: fixed;
  left: 0;
  top: 0;
  height: 126px;
  width: 100%;
  background: #393536;
  z-index: -2;
  transition: 0.5s ease;
  transform: translateY(-135%);
  /* filter: drop-shadow(0px 4px 39px rgba(51, 142, 205, 0.39)); */
}
header.sticky{
  padding: 44px 0;
}
header.sticky::before {
  transform: translateY(0%);
}

